


























import Vue from 'vue'
import { mapActions } from 'vuex'
import { ActionTypes } from '@/components/resume/store/action-types'

export default Vue.extend({
  name: 'App',
  data: () => ({
    links: [
      {
        text: 'Home',
        route: '/'
      },
      {
        text: 'Resume',
        route: '/resume'
      }
    ]
  }),
  methods: {
    ...mapActions('resume', [ActionTypes.RESET_JOB_HISTORY])
  },
  mounted () {
    this[ActionTypes.RESET_JOB_HISTORY]()
  }
})
